import React from 'react';

import "./TradingView.scss";
import {TradingPanel, PanelType} from "./TradingPanel";
import {BalanceView} from "./BalanceView";

export class TradingView extends React.Component {
    render() {
        return (
            <div id="tradingView">
                <div className="tradingHeader">
                    <label className="tradingTitle">BTC</label>
                    <BalanceView amount="0.0285" symbol="BTC" />
                </div>
                <div className="panelsContainer">
                    <TradingPanel type={PanelType.HIGH_STOP} />
                    <div className="separator" />
                    <TradingPanel type={PanelType.LOW_STOP} />
                </div>
            </div>
        );
    }
}


