import React from 'react';

import "./TradingPanel.scss";

export const PanelType = {
    HIGH_STOP: "HIGH_STOP",
    LOW_STOP: "LOW_STOP"
};

export class TradingPanel extends React.Component {
    render() {
        const { type } = this.props;

        return (
            <div className="tradingPanel">
                <span className="title">{type === PanelType.LOW_STOP ? "Low Stop" : "High Stop"}</span>
                <table className="detailsBox">
                    <tbody>
                        <tr>
                            <td><label>Stop:</label></td>
                            <td><input type="text" /></td>
                        </tr>
                        {
                            type === PanelType.HIGH_STOP ?
                                <tr>
                                    <td><label>Trailing (%):</label></td>
                                    <td><input type="text" /></td>
                                </tr> : null
                        }
                        <tr>
                            <td><label>Limit (%):</label></td>
                            <td><input type="text" /></td>
                        </tr>
                        <tr>
                            <td><label>Limit:</label></td>
                            <td><input type="text" /></td>
                        </tr>
                        <tr>
                            <td><label>Amount:</label></td>
                            <td><input type="text" /></td>
                        </tr>
                        <tr>
                            <td><label>Total:</label></td>
                            <td><input type="text" disabled={true} /></td>
                        </tr>
                    </tbody>
                </table>
                <div className="buttons">
                    <ul className="tradingPercentSelection">
                        <li><button>25%</button></li>
                        <li><button>50%</button></li>
                        <li><button>75%</button></li>
                        <li><button>100%</button></li>
                    </ul>
                    <button className="tradeButton">Confirm</button>
                </div>
            </div>
        )
    }
}


