import {ActionTypes} from "../actions/ActionTypes";

const initialAuthState = {
    loggedIn: false
};

export const authReducer = (state, action) => {
    const authActionsMap = {
        [ActionTypes.LOGIN]: handleAuth(true),
        [ActionTypes.LOGOUT]: handleAuth(false)
    };

    if (action.type in authActionsMap) {
        return authActionsMap[action.type](state, action);
    } else {
        return Object.assign({}, state, initialAuthState);
    }
};

const handleAuth = loggedIn => state => {
    return Object.assign({}, state, {
        loggedIn: loggedIn
    });
};


